.workspace-modal-create-title {
    font-size: 0.875rem;
    color: #5e6c84;
    padding: 0.5rem;
    padding-inline-start: 44%;
    padding-inline-end: 1rem;
    a {
        font-weight: 700;
    }
}

.create {
    padding-block-start: 8px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Noto Sans', 'Ubuntu', 'Droid Sans', 'Helvetica Neue', sans-serif;
    article {
        padding: 0.5rem;
        bottom: 10px;
        display: flex;
        flex-direction: column;
        margin: 0;
        div {
            gap: 0.25rem;
            width: 100%;
            display: flex;
            align-items: center;
            padding: 0 0.5rem 0.5rem;
            img {
                width: 1rem;
                height: 1rem;
            }
            h4 {
                margin: 0;
                font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Noto Sans', 'Ubuntu', 'Droid Sans', 'Helvetica Neue', sans-serif;
                color: #172b4d;
                font-size: rem(14px);
                font-weight: 400;
            }
        }
        p {
            padding: 0 4px;
            bottom: 10px;
            width: 100%;
            color: #5e6c84;
            font-size: 0.75rem;
            font-weight: 400;
            line-height: 1rem;
            margin: 0;
        }
    }
    article:hover {
        border: none;
        background: rgba(9, 30, 66, 0.04);
        cursor: pointer;
    }
    svg {
        height: 1rem;
        width: 1rem;
    }
}

