.group-container {
  min-width: rem(272px);
  width: rem(272px);
  min-height: rem(72px);
  max-height: 100%;
  overflow-y: hidden;
  border-radius: 3px;
  background-color: #ebecf0;
  align-items: flex-start;
  & > .group-data-container {
    width: calc(100% - 4px);
    overflow-y: auto;
  }
  ::-webkit-scrollbar {
    width: 8px;
  }
  ::-webkit-scrollbar-track {
    background: #091e4214;
    border-radius: 50rem;
  }
  ::-webkit-scrollbar-thumb {
    border-radius: 50rem;
    background: #091e4214;
  }
  & > div {
    width: 100%;
  }
  & > .group-header {
    min-height: rem(40px);
    width: 100%;
    padding: rem(6px) rem(6px) rem(6px) rem(16px);
    padding-block-start: 10px;
    :first-child {
      flex: 1;
      font-weight: 500;
    }
    input {
      font-size: rem(14px);
      font-weight: 500;
      font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
        Noto Sans, Ubuntu, Droid Sans, Helvetica Neue, sans-serif;
      padding-inline-start: rem(6px);
      border-radius: 3px;
      width: 100%;
      height: rem(28px);
    }
    input:focus {
      outline-color: #2684ff;
    }
    svg {
      height: 20px;
      width: 20px;
    }
  }
  & > .input {
    padding: rem(6px) rem(8px);
  }
  .task-list-container {
    width: 100%;
    gap: 0.5rem;
    padding: 0 4px 0 8px;
    max-height: inherit;
  }

  .add-item {
    row-gap: 0.5rem;
    min-height: rem(40px);
    width: 100%;
    border-radius: 3px;
    padding: 0.25rem 0.25rem 0.5rem 0.5rem;
    button {
      gap: 4px;
      border-radius: 3px;
      text-align: start;
      width: 100%;
      background-color: inherit;
      color: #5e6c84;
      border: none;
      cursor: pointer;
      span::before {
        font-size: rem(20px);
        content: '+';
      }
    }
    button:hover {
      background-color: #091e4214;
      color: #172b4d;
    }
  }
  .new-item {
    padding: 10px 8px;
    width: 100%;
    gap: 10px;
  }
  .new-item > textarea {
    padding: 0.5rem;
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans,
      Ubuntu, Droid Sans, Helvetica Neue, sans-serif;
    width: 100%;
    resize: none;
    border-radius: 3px;
    box-shadow: 0 1px 0 rgb(22 24 27 / 25%);
    min-height: 60px;
    border: none;
    outline: none;
  }
  .new-item > .new-item-actions {
    width: 100%;
  }
  .new-item > .new-item-actions > button {
    background-color: #0079bf;
    border-radius: 3px;
    color: #fff;
    padding: rem(8px) rem(12px);
    margin-inline-end: 12px;
  }
  .new-item > .new-item-actions > a {
    color: #6b778c;
    font-weight: 700;
    font-size: 20px;
    flex: 1;
  }
}
