.checklist-container {
    // margin-bottom: rem(35px);
    .checklist-header {
        font-size: 1rem;
        color: #172b4d;
        .header-icon {
            font-size: 24px;
            color: #42536f;
            margin-right: 0.7rem;
        }
        .checklist-header-title {
            width: 100%;
        }
        button {
            align-items: center;
            background-color: #091e420a;
            border: none;
            border-radius: 3px;
            color: #172b4d;
            cursor: pointer;
            font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Droid Sans, Helvetica Neue, sans-serif;
            font-size: 14px;
            font-weight: 400;
            justify-content: center;
            line-height: 20px;
            padding: 6px 12px;
            text-decoration: none;
        }
        button:hover {
            background-color: #091e4214;
        }
        .last-btn {
            margin-top: 20px;
            margin-bottom: 20px;
        }
        h3 {
            margin-bottom: rem(4px);
        }
        .checkbox-info {
            .checkbox {
                font-size: small;
                color: none;
                border-radius: 2px;
                // box-shadow: inset 0 0 0 2px #dfe1e6;
                cursor: pointer;
                height: 16px;
                margin: 4px;
                width: 16px;
            }
        }
    }
    .MuiBox-root {
        p {
            padding-inline-start: rem(4px);
        }
    }
}