.upper-header {
    width: 100%;
    padding: 0.5rem;
    flex-wrap: wrap;
    row-gap: 0.5rem;
    .title-container {
        height: 100%;
    }
    .info-details {
        gap: 0.5rem;
        flex: 1;
        justify-content: flex-start;
        .board-stats {
            width: rem(92px);
            img {
                width: 1rem;
                height: 1rem;
            }
        }
        input {
            font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
            height: 100%;
            font-size: rem(18px);
            font-weight: 600;
            padding: 0 rem(12px);
            border: none;
            color: #172b4d;
            background-color: #fdfdfd7a;
            border-radius: rem(3px);
        }
        .rating-container {
            background-color: #fdfdfd7a;
            border-radius: 3px;
            width: 2rem;
            height: 2rem;
        }
        .workspace-type {
            min-width: 10rem;
            button {
                color: #172b4d;
                background-color: transparent;
            }
        }
    }
    .board-header-btn,
    .workspace-type {
        background-color: #fdfdfd7a;
        width: fit-content;
        height: rem(32px);
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        border-radius: 3px;
        font-size: rem(14px);
        padding: rem(6px) rem(5px);
        color: #172b4d;
    }
    .board-header-btn:hover,
    .workspace-type:hover,
    input:hover {
        filter: invert(1);
        cursor: pointer;
    }
    .new-member {
        background-color: #0179bf;
        height: rem(32px);
        max-width: rem(75px);
        gap: 0.25rem;
        margin-inline-end: 0.25rem;
        a {
            text-transform: none;
        }
    }
    .new-member:hover {
        background-color: #1565c0;
    }
    .board-header-btn-divider {
        margin: 0 0.25rem 0;
        height: 1rem;
        border-left: 1px solid #fdfdfd7a;
    }
    .upper-options {
        gap: 0.25rem;
    }
    .automation {
        width: rem(120px);
    }
    .filter {
        width: rem(78px);
    }
    .menu-container {
        min-width: rem(117px);
        gap: rem(4px);
        button {
            background-color: transparent;
        }
    }
    .rating-container:hover {
        cursor: pointer;
        background-color: #00000029;
    }
}